.popup {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    position: fixed;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    display: flex;
    opacity: 0;
    overflow: hidden;
    visibility: hidden;
    transition: visibility .5s, opacity .5s linear;
}